@import "../../style/variable.scss";
.radio-group-container {
  width: 150px;
  margin: auto;
  margin-top: 20px;
  .radio-group {
    width: 100%;
    &__header {
      height: 36px;
      background-color: #f4f4f4;
      display: flex;
      flex-direction: row;
      align-items: center;
      border-radius: 3px;
      font-size: $sizeHeading;
      line-height: 17px;
      font-family: $fontPrimary;
      padding: 10px 5px 10px 10px;
      span {
        margin-left: 10px;
      }
    }
    &__body {
      width: 100%;
      border: 1px solid #707070;
      border-radius: 0 3px;
      padding: 10px;

      form {
        margin-top: 20px;
      }
    }
    &__search {
      width: 100%;
      position: relative;
      img {
        position: absolute;
        right: 0px;
        top: 6px;
      }
      input {
        width: 100%;
        border: none;
        border-bottom: 1px solid #4d4d4d;
        font-size: 13px;
        line-height: 15px;
        padding-left: 5px;
        padding-right: 15px;
        font-family: $fontPrimary;
        &:focus {
          outline: none;
        }
      }
    }
    // &__item{
    //     margin-bottom: 10px;
    //     font-size: 14px;
    //     line-height: 17px;
    //     height:17px;
    //     font-family: 'Calibri';
    //     display: flex;
    //     align-items: center;
    //     input[type="radio"]{
    //         margin-right: 10px;
    //         width: 15px;
    //         height: 15px;
    //     }
    //     &:last-child{
    //         margin-bottom: 0;
    //     }
    // }
  }
}
